import React, {Component} from 'react';
import TableCell from "./tableCell/tableCell";
import {Link} from "react-router-dom";

class App extends Component {
    _renderRow = () => {
        let hoursObject = this.props.item;
        let counter = 0;
        let firstCell = this.props.name;
        if(this.props.link){
            firstCell = <Link  to={"/trida/" + this.props.name}>{this.props.name}</Link>;
        }

        let arrayCells = [<td className="date" key={this.props.name} onClick={this.props.handleClickClass ?
            () => this.props.handleClickClass(this.props.name) : null}>{firstCell}</td>];
        for (let key in hoursObject) {
            let hours = hoursObject[key];

            let hourNumber = key.replace("h", "");
            if (hourNumber > counter) {
                for (let i = counter; i < hourNumber; i++) {
                    arrayCells.push(<td key={key + counter + "empty"}></td>);
                    counter++;
                }
            }
            arrayCells.push(<TableCell hours={hours} key={key + "cell"} date={this.props.date}/>)
            counter++;
        }

        if (counter < 12) {
            for (let i = counter; i <= 12; i++) {
                arrayCells.push(<td key={i + "emptyAfter"}></td>)
            }
        }

        return <tr className="timetable">
            {arrayCells}
        </tr>;
    };

    render() {
        return this._renderRow()
    }
}

export default App;



