import React, {Component} from 'react';
import TableRow from "./tableRow/tableRow"
import {Link} from "react-router-dom";

const days = ["Po", "t", "St", "t", "P"];

class Timetable extends Component {
    constructor(props){
        super(props);
        window.scrollTo(0,0);
    }
    _renderTimeTable = () => {

        const {date, json, original, hours, selectedClass} = this.props;
        const arrayHeader = [];
        let arrayTimeHours = [<th key="empty"></th>];
        for (let keyHour in hours) {
            const timeHour = hours[keyHour];
            arrayTimeHours.push(
                <th key={keyHour} className="hour">
                    {keyHour.replace("h", "")}<span>{timeHour.from}-{timeHour.to}</span>
                </th>)

        }
        arrayHeader.push(<tr key="class">
            <th colSpan="14" className="class">rozvrh {selectedClass}</th>
        </tr>);
        arrayHeader.push(<tr key={"hours"} className="hours">{arrayTimeHours}</tr>)

        let array = [arrayHeader];
        if (json) {
            const itemsJson = json.items;
            const itemsOrig = original.items;
            itemsJson.forEach((item, i) => {
                if (Object.keys(item).length > 0) {
                    array.push(<TableRow item={item} name={days[i]} key={i + "class"} date={date} numberOfDay={i}/>)
                } else {
                    array.push(<TableRow item={itemsOrig[i]} name={days[i]} key={i + "class"} date={date} numberOfDay={i}/>)
                }

            });
            return array;
        } else {
            return <h2>Data nebyla nalezena</h2>
        }


    }

    render() {
        return (
            <div>
                <table>
                    <tbody>
                    {this._renderTimeTable()}
                    </tbody>
                </table>
                <span className="spoji class">spoji</span>
                <span className="zmena class">zmena</span>
                <span className="supluje class">supluje</span>
                <span className="odpada class">odpada</span>
                <span className="navic class">navic</span>
                <span className="vymena class">vymena</span>
                <Link to="/">
                    <button className="button-today">Zpt na suplovn</button>
                </Link>
            </div>
        );
    }
}

export default Timetable;



