import React, {Component} from 'react';
import TableCellContent from "./tableCellContent";
import {getWeekNumber} from "../../../utils/utils";

class App extends Component {
    _renderFlow = () => {
        const {hours, date} = this.props;
        const week =  (getWeekNumber(date) % 2) === 0 ? "S: " : "L: ";
        const arrayCellContent = [];

        if(hours){
            this.numberOfHours = this.getValidWeekHour(hours, week);
            hours.forEach((hour, index) => {
                if (hour.week) {
                    if (hour.week === week) {
                        arrayCellContent.push(<TableCellContent item={hour} numberOfHours={this.numberOfHours } key={index}/>);
                    }
                } else {
                    arrayCellContent.push(<TableCellContent item={hour} numberOfHours={this.numberOfHours } key={index}/>);
                }

            });
        }else {
            return (<td></td>);
        }


        if (arrayCellContent.length === 0) {
            return (<td></td>);
        } else {
            return (<td className={"hour-1 fill " + (this.numberOfHours === 1 ? "full" : "split")}>{arrayCellContent}</td>);
        }
    };

    getValidWeekHour = (hour, week) => {
        let counter = 0;
        hour.forEach((h) => {
            if (h.week) {
                if (h.week === week) {
                    counter++;
                }
            } else {
                counter++;
            }

        });
        return counter;
    };

    render() {
        return this._renderFlow()
    }
}

export default App;



