import React from 'react';
import diacriticts from 'diacritics';

const tableCellContent = ({item, index, numberOfHours}) => {
    const style = numberOfHours === 1 ? {} : {height: 100 / numberOfHours + "%"};
    return (
        <div
            key={index}
            style={style}
            className={getChangeClass(item)}
        >
            <div>
                {item.subject} {item.group} <span>{item.teacher} {item.room}</span>
            </div>
        </div>
    )
};

const getChangeClass = (object) => {
    return object.change ? diacriticts.remove(object.change.split(" ")[0]) : "";
};

export default tableCellContent;


