import React, {Component} from 'react';
import TableRow from "./tableRow/tableRow"
import {splitArrayIntoChunks} from "../utils/utils";

const RESOLUTION_WIDTH = 3830;
class Timetable extends Component {
    _renderTable = (object, keys, windowWidth) => {
        const {numberOfDay, date, json, suplDates, original, hours} = object;

        let timeTable;
        if (suplDates && (date.getTime() > new Date(suplDates[suplDates.length - 1]).getTime() || date.getTime() < new Date(suplDates[0]).getTime())) {
            timeTable = original;
        } else {
            timeTable = json;
        }
        let array = [];
        let first = true;

        keys.forEach((key) => {
            const clas = timeTable[key];
            if (first) {
                let arrayTimeHours = [<th key="empty"></th>];
                for (let keyHour in hours) {
                    const timeHour = hours[keyHour];
                    arrayTimeHours.push(
                        <th key={keyHour} className="hour">
                            {keyHour.replace("h", "")}<span>{timeHour.from}-{timeHour.to}</span>
                        </th>)

                }
                if(windowWidth < RESOLUTION_WIDTH){
                    array.push(<tr key="class">
                        <th colSpan="14" className="class">
                            rozvrh {date.getDate() + ". " + Number(date.getMonth() + 1) + "."}</th>
                    </tr>)
                }

                array.push(<tr key={"hours"} className="hours">{arrayTimeHours}</tr>)
                first = !first; //TODO
            }
            if (this.getTrueIfChange(clas)) {
                array.push(<TableRow
                    item={clas.items[numberOfDay]}
                    name={key} key={key + "class"}
                    numberOfDay={numberOfDay}
                    date={date}
                    handleClickClass={this._handleClickClass}
                    link
                />)
            }
        })

        return array;
    }

    getTrueIfChange = (room) => {
   /*     let checkChange = false;
        let hoursObject = room.items[this.props.state.numberOfDay];

        for (let key in hoursObject) {
            let hours = hoursObject[key];
            if(!hours) debugger
            hours.forEach((hour) => {
                    if (hour.change) {
                        checkChange = true;
                    }
                }
            )
        }*/
        //TODO pro vpis pouze zmn
        //return checkChange
        return true;

    };

    _handleClickClass = (classKey) => {
        const {changeState} = this.props;
        changeState({
            selectedClass: classKey,
        })
    }

    _handleClick = (e) => {
        const date = this.props.state.date;
        const {changeState} = this.props;
        let index = date.getDay() - 1;
        let change = 0;
        if (e.target.innerHTML === "rozvrh pedel den") {
            index -= 1;
            change = -1;
        } else {
            index += 1;
            change = 1;
        }

        if (index > 4) {
            const diff = (7 + 1) - index;
            const objectDate = new Date(date.getFullYear(), date.getMonth(), date.getDate() + diff);
            changeState({
                date: objectDate,
                numberOfDay: objectDate.getDay() - 1,
            })
        } else if (index === -1) {
            const objectDate = new Date(date.getFullYear(), date.getMonth(), date.getDate() + index - 2);
            changeState({
                date: objectDate,
                numberOfDay: objectDate.getDay() - 1,
            })
        } else {
            const objectDate = new Date(date.getFullYear(), date.getMonth(), date.getDate() + change);
            changeState({
                date: objectDate,
                numberOfDay: objectDate.getDay() - 1,
            })
        }
    };
    _renderTables = () => {
        const tableArray = [];
        const windowWidth = this.props.windowWidth;
        const state = this.props.state;
        const {original} = state;
        if (original) {
            if (windowWidth > RESOLUTION_WIDTH) {
                const keys = Object.keys(original);
                const chunks = splitArrayIntoChunks(keys, 3);
                chunks.forEach((chunOfKeys, i) => {
                    tableArray.push(
                        <div className="tableK" key={i}>
                            <table key={i}>
                                <tbody>
                                {this._renderTable(state, chunOfKeys, windowWidth)}
                                </tbody>
                            </table>
                        </div>
                    )
                })
            } else {
                const keys = Object.keys(original)
                tableArray.push(
                    <table key={0}>
                        <tbody>
                        {this._renderTable(state, keys, windowWidth)}
                        </tbody>
                    </table>
                )
            }
            return tableArray;
        }
        return null;
    }

    render() {
        const date = this.props.state.date;
        const resolution = this.props.windowWidth;
        const header = resolution > RESOLUTION_WIDTH ? date ? <div className="suplHead">rozvrh {date.getDate() + ". " + Number(date.getMonth() + 1) + "."}</div> : null : null;
        const supl = this._renderTables();
        if(supl){
            return (
                <div>
                    <div>
                        {header}
                        {this._renderTables()}
                    </div>
                    <span className="spoji class">spoji</span>
                    <span className="zmena class">zmena</span>
                    <span className="supluje class">supluje</span>
                    <span className="odpada class">odpada</span>
                    <span className="navic class">navic</span>
                    <span className="vymena class">vymena</span>
                    <span className="presun class">presun</span>
                    <button onClick={this._handleClick} className="button-today">rozvrh pedel den</button>
                    <button onClick={this._handleClick} className="button-next">rozvrh dal den</button>
                </div>
            );
        }else {
            return <h2>Natm data ...</h2>
        }

    }
}

export default Timetable;



