import React, {Component} from 'react';
import SuplTable from "./components/suplTable";
import TimeTable from "./components/timeTable";
import {
    BrowserRouter as Router,
    Route,
    Switch,
    Redirect
} from 'react-router-dom';
require('disable-react-devtools');

const myHeaders = new Headers();
const myInit = {
    method: 'GET',
    headers: myHeaders,
    mode: 'cors',
    cache: 'default'
};


class App extends Component {
    constructor(props) {
        super(props);
        this.state = {windowWidth: window.screen.availWidth};

        this.updateJson();
        setInterval(() => this.updateJson(), 20 * 60 * 1000)
    }

    componentDidMount() {
        window.addEventListener("resize", this.updateWindowWidth);
    }

    updateWindowWidth = () =>{
        this.setState({
            windowWidth: window.screen.availWidth,
        })
    };

    fetchJson = (url) => {
      return  fetch(url, myInit).then((response) => {
            if(response.ok) return response.json();
        })
    }

    updateJson = () => {
        const url = process.env.NODE_ENV === 'production' ? "/api/json" : 'https://localhost:4430/api/json';
        this.fetchJson(url).then((data) => {
            let date = new Date(data.date);

            this.setState({
                json: data.jsonObject,
                numberOfDay: date.getDay() - 1,
                date: date,
                original: data.original,
                suplDates: data.suplDates,
                hours: data.hours,
                selectedClass: null,
            });

        }).catch((err) => {
            console.log(err)
        })
    }
    _renderFlow = () => {
        const state = this.state;
        const {selectedClass, json, hours } = state;
        if (selectedClass) {
            return <TimeTable json={json[selectedClass]} hours={hours} selectedClass={selectedClass}/>
        } else {
            return <SuplTable state={state} changeState={this.changeState}/>
        }
    };

    changeState = (newState) => {
        this.setState(newState)
    }
    SuplTableHOC = () => {
        return <SuplTable state={this.state} changeState={this.changeState} windowWidth={this.state.windowWidth}/>
    }
    TimeTableHOC = (props) => {
        const {json, hours, original} = this.state;
        const selectedClass = props.match.params.clas.toUpperCase();
        if(json) {
            return <TimeTable json={json[selectedClass]} hours={hours} selectedClass={selectedClass} original={original[selectedClass]} {...props}/>
        }else {
            return <h2>Natm data ...</h2>
        }
    }

    render() {
        return (
            <div>
                <Router>
                    <div>
                        <Switch>
                            <Route exact path="/" component={this.SuplTableHOC}/>
                            <Route path="/trida/:clas" component={(props) => this.TimeTableHOC(props)}/>
                            <Route path="*" component={() => <Redirect to="/" />} />
                        </Switch>
                    </div>
                </Router>

                <footer className="footer-basic-centered">
                    <p className="footer-company-motto">Vytvoil Martin Skalick</p>
                    <p className="footer-company-name">2017</p>
                    <p className="footer-company-name">Ppadn bugy prosm hlaste na skalicky.martas@gmail.com</p>
                </footer>
            </div>
        )
    }

}

export default App;



